"use client";

import { useMemo, useState } from "react";
import WodSection from "@/components/athlete/WodSection";
import { mockWod } from "@/lib/mockWod";

export default function WodPage() {
  const [mode, setMode] = useState<"rx" | "scaled">("rx");

  const dateLabel = useMemo(() => {
    const d = new Date(mockWod.date + "T00:00:00");
    return d.toLocaleDateString("es-ES", {
      weekday: "long",
      day: "2-digit",
      month: "2-digit",
    });
  }, []);

  return (
    <div>
      <div className="flex items-start justify-between gap-3">
        <div>
          <h1 className="text-xl font-semibold">WOD</h1>
          <div className="mt-1 text-sm text-gray-500">
            {dateLabel} • {mockWod.title}
          </div>
        </div>

        <div className="rounded-xl border border-gray-200 bg-white p-1 text-sm">
          <button
            onClick={() => setMode("rx")}
            className={`rounded-lg px-3 py-1 ${
              mode === "rx" ? "bg-gray-900 text-white" : "text-gray-700"
            }`}
          >
            RX
          </button>
          <button
            onClick={() => setMode("scaled")}
            className={`rounded-lg px-3 py-1 ${
              mode === "scaled" ? "bg-gray-900 text-white" : "text-gray-700"
            }`}
          >
            Scaled
          </button>
        </div>
      </div>

      <div className="mt-3 rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
        <div className="text-sm font-semibold">Intención</div>
        <p className="mt-1 text-sm text-gray-700">{mockWod.intent}</p>
      </div>

      <div className="mt-3 grid gap-3">
        <WodSection title="Warm-up" items={mockWod.warmup} />
        {mockWod.strength && <WodSection title="Strength" items={mockWod.strength} />}
        <WodSection title="Metcon" items={mockWod.metcon} />
        <WodSection
          title={mode === "rx" ? "Standards (RX)" : "Opciones (Scaled)"}
          items={mode === "rx" ? mockWod.rx : mockWod.scaled}
        />
        {mockWod.notes && <WodSection title="Notas del coach" items={mockWod.notes} />}
      </div>
    </div>
  );
}
